export function login (param) {
  return new Promise((resolve, reject) => {
    console.log("sys.js---===>login",param, api.LOGIN);
    axios({
      url: api.LOGIN ,
      method: 'post',
      params:param
   }).then(response => {
      console.log("sys.js---===>login===>success",response);
      resolve(response.data);
    }).catch((error) => {
      console.log("sys.js---===>login===>error",error);
      reject(defaultValue.login)
    })
  })
}

export function userLogout(params) {
  // const userList = {total:defaultValue.userList.total,records:defaultValue.userList.records.reverse()}
  var token = window.localStorage.getItem('imp-sid');
  // console.log("++++token:", token);
  axios.defaults.headers.common['token'] = token;
  return new Promise((resolve, reject) => {
    axios.post(api.LOGOUT+params, {}).then(response => {
      resolve(response.data);
    }, err => {
      reject(err);
    })
      .catch((error) => {
        reject(error)
      })
  })
}

