<template>
  <imp-panel :title="form.userId ? '编辑':'新增用户'">
    <el-form ref="form" :model="form" label-width="180px">
      <el-form-item label="用户名">
        <el-input v-model="form.userName"></el-input>
      </el-form-item>
      <el-form-item label="登录用户名">
        <el-input v-model="form.userNumber"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="form.password"></el-input>
      </el-form-item>
      <!-- <el-form-item label="确认密码">
        <el-input v-model="confirmPassword"></el-input>
      </el-form-item> -->
      <!-- <el-form-item label="工号">
        <el-input v-model="form.no"></el-input>
      </el-form-item>
      <el-form-item label="固定电话">
        <el-input v-model="form.phone"></el-input>
      </el-form-item> -->
      <!-- <el-form-item label="状态">
        <el-radio-group v-model="form.status">
          <el-radio :label="0">未激活</el-radio>
          <el-radio :label="1">已激活</el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="用户类型">
        <el-radio-group v-model="form.userRole">
          <el-radio label="2">管理员</el-radio>
          <el-radio label="1">普通用户</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- <el-form-item label="备注">
        <el-input type="textarea" v-model="form.remarks"></el-input>
      </el-form-item> -->
      <el-form-item>
        <el-button type="info" @click="onEditSubmit" v-if="form.userId">保存</el-button>
        <el-button type="primary" @click="onSubmit" v-else>立即创建</el-button>
      </el-form-item>
    </el-form>
  </imp-panel>
</template>
<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import * as sysApi from '../../services/sys'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        confirmPassword:'',
        form: {
          userId: 0,
          password: "",
          userNumber: "",
          userName: "",
          userRole: "1",
        }
      }
    },
    created(){
      this.loadData();
    },
    methods: {
      onSubmit(){
        console.log("角色：", );
        sysApi.addUser({
          userNumber:this.form.userNumber,
          password: this.form.password,
          userName: this.form.userName,
          userRole: parseInt(this.form.userRole)
        })
          .then(res => {
            this.$router.push({path: 'userList'})
          }).catch(res => {
            console.log("add user error", res);
          })
      },
      onEditSubmit(){
        console.log("update user", this.form);
        sysApi.updateUser(this.form)
          .then(res => {
            this.$router.push({path: 'userList'})
          }).catch(res => {
            console.log("update user error", res);
          })
      },
      loadData(){
        if (this.$route.query && this.$route.query != null && this.$route.query.id && this.$route.query.id != null) {
          this.form.userId = this.$route.query.id;
          sysApi.queryUser( this.form.userId)
            .then(res => {
              this.form = res.data;
            })
        }
      }
    }
  }
</script>



