
<template>
  <!-- <div style="position:absolute"> -->
  <!-- <imp-panel> -->
  <div >
    <div class="historyTableStyle" >
      <el-table
        border
        :data="historyOrder.slice((currentPage-1)*pageSize,currentPage*pageSize)">
        <el-table-column prop="number" label="序号" width="100">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="company" label="公司名称">
          <template slot-scope="scope">
            <span style="margin-left: 0px">{{ scope.row.company }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="IP地址">
          <template slot-scope="scope">
            <span style="margin-left: 0px">{{ scope.row.ipAddress }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="from" label="派单公司名称">
          <template slot-scope="scope">
            <span style="margin-left: 0px">{{ scope.row.fromCompany }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="日期">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 0px">{{ scope.row.date }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="承接状态" >
          <template slot-scope="scope">
            <span style="margin-left: 0px">{{ getStatusText(scope.row.status) }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页器 -->
			<!-- <div class="pageNumberStyle" style="margin-top:15px;">
				<el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="historyOrder.length">
				</el-pagination>
			</div> -->
  </div>
<!-- </imp-panel> -->
</template>

<script>
import panel from "../components/panel.vue"
export default {
  components: {
      'imp-panel': panel
    },
  data() {
    return {
      reportnum: null,
      reportToday: 0,
      historyOrder: [{
          id:0,
          company:"张三刻章",
          ipAddress:"10.10.0.12",
          fromCompany:"好运来科技有限公司",
          date: '2016-05-02 15:35:06',
          status: 2,
        }, {
          id:1,
          company:"王五刻章",
          ipAddress:"10.10.0.13",
          fromCompany:"幸福科技有限公司",
          date: '2016-05-02 15:36:06',
          status: 3,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }, {
          id:2,
          company:"李四刻章",
          ipAddress:"10.10.0.14",
          fromCompany:"万家超市",
          date: '2016-05-02 15:40:06',
          status: 2,
        }],
        loadingMore: false, // 是否正在加载更多
        page: 1, // 当前页码
        pageSize: 10000, // 每页数据量
        total: 10000, // 总数据量，根据实际情况获取
        currentPage:1,
    }
  },
  mounted() {

  },
  methods: {
    handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.currentPage = 1;
			this.pageSize = val;
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.currentPage = val;
		},
    // 初始加载数据
    fetchData() {
      // 模拟请求数据
      this.historyOrder = Array.from({ length: this.pageSize }, (_, index) => ({
        id:10,
        company:"李四刻章",
        ipAddress:"10.10.0.14",
        fromCompany:"万家超市",
        date: '2016-05-02 15:40:06',
        status: 2,
        // date: '2016-05-02',
        // name: `John${this.page * this.pageSize + index}`,
        // address: `No. ${this.page * this.pageSize + index} Lake Park`
      }));
    },
    // 下滑加载更多
    handleScroll(event) {
      const target = event.target;
      // 计算是否到达底部
      console.log("滑动：", target);
      if (target.scrollHeight - target.scrollTop <= target.clientHeight && this.historyOrder.length < this.total) {
        if (!this.loadingMore) {
          this.loadingMore = true;
          // 模拟异步加载更多数据
          setTimeout(() => {
            this.page++;
            this.fetchData();
            this.loadingMore = false;
          }, 1000);
        }
      }
    },
    handleEdit(index, row) {
        console.log(index, row);
      },
    handleDelete(index, row) {
      console.log(index, row);
    },
    getStatusText(status){
      var message = "待接单";
      switch (status){
        case 0:
          message = "待接单";
          break;
        case 1:
          message = "制作中";
          break;
        case 2:
          message = "完成";
          break;
        case 3:
          message = "失败";
          break;
      }
      return message;
    },
    getButtonText(status){
      var message = "承接";
      switch (status){
        case 0:
          message = "承接";
          break;
        case 1:
        case 2:
          message = "完成";
          break;
      }
      return message;
    },
    getChromeStatusText(status){
      var message = "监测正常";
      switch (status){
        case 0:
          message = "监测正常";
          break;
        case 1:
          message = "监测异常";
          break;
      }
      return message;
    },
  }
}
</script>


<style >
  .historyTableStyle {
    width: 100%;
    height: 20vh;
  }

  .el-table th {
    background-color: #f0f0f0; /* 表头背景色 */
    color: #606266; /* 表头字体颜色 */
  }
  .titleStyle{
  display: flex;
  align-items: center; /* 垂直居中 */
  justify-content: flex-start; /* 水平居左 */
  height: 30px; /* 容器高度，可以根据需要调整 */
  background-color:#d1dddb;
  padding-left: 8px;
  font-style: oblique;
  font-weight: bold;
  }
  .pageNumberStyle{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 50%; /* or any other width */
    text-align: center;
    margin-bottom: 10px;
  }
</style>



