<template>

	<imp-panel>
	  <h3 class="box-title" slot="header" style="width: 100%;">
		<el-row style="width: 100%;">
		  <el-col :span="12">
			<router-link :to="{ path: 'companyAdd'}">
			  <el-button type="primary" icon="plus">新增</el-button>
			</router-link>
		  </el-col>
		  <!-- <el-col :span="12">
			<div class="el-input" style="width: 200px; float: right;">
			  <i class="el-input__icon el-icon-search"></i>
			  <input type="text" placeholder="输入用户名称" v-model="searchKey" @keyup.enter="search($event)"
					 class="el-input__inner">
			</div>
		  </el-col> -->
		</el-row>
	  </h3>
	  <div slot="body">
		<el-table
		  :data="tableData.rows"
		  border
		  style="width: 100%"
		  v-loading="companyLististLoading"
		  @selection-change="handleSelectionChange">
		  <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
		  <!-- <el-table-column
			prop="id"
			type="selection"
			width="50">
		  </el-table-column> -->
		  <!-- <el-table-column
			label="照片" width="76">
			<template slot-scope="scope">
			  <img :src='scope.row.avatar' style="height: 35px;vertical-align: middle;" alt="">
			</template>
		  </el-table-column> -->
		  <el-table-column
			prop="companyId"
			label="公司ID">
		  </el-table-column>
		  <el-table-column
			prop="companyName"
			label="公司名称">
		  </el-table-column>
		  <el-table-column
			prop="companyTaxNumber"
			label="公司税号">
		  </el-table-column>
		  <el-table-column
			prop="createTime"
			label="创建时间">
		  </el-table-column>
		  <el-table-column
		  	prop="adminUser"
			label="管理员">
			<template slot-scope="scope">
				<span >{{ scope.row.adminUser ? scope.row.adminUser.userName : 'asas' }}</span>
			</template>
		  </el-table-column>
		  <el-table-column label="操作" width="285">
			<template slot-scope="scope">
			  <!-- <el-button
				size="small"
				type="default"
				icon="edit"
				@click="handleEdit(scope.$index, scope.row)">编辑
			  </el-button> -->
			  <el-button
				size="small"
				type="info"
				icon="setting"
				@click="handleRoleConfig(scope.$index, scope.row)">配置管理员
			  </el-button>
			  <el-button
				size="small"
				type="danger"
				@click="handleDelete(scope.$index, scope.row)">删除
			  </el-button>
			</template>
		  </el-table-column>
		</el-table>
  
		<!-- <el-pagination
		  @size-change="handleSizeChange"
		  @current-change="handleCurrentChange"
		  :current-page="tableData.pagination.pageNo"
		  :page-sizes="[5, 10, 20]"
		  :page-size="tableData.pagination.pageSize"
		  layout="total, sizes, prev, pager, next, jumper"
		  :total="tableData.pagination.total">
		</el-pagination> -->
  
		<!-- <el-dialog title="配置用户角色" v-model="dialogVisible" size="tiny">
		  <div class="select-tree">
			<el-scrollbar
			  tag="div"
			  class='is-empty'
			  wrap-class="el-select-dropdown__wrap"
			  view-class="el-select-dropdown__list">
			  <el-tree
				ref="roleTree"
				:data="roleTree"
				show-checkbox
				check-strictly
				node-key="userId" v-loading="dialogLoading"
				props="userName">
			  </el-tree>
			</el-scrollbar>
		  </div>
		  <span slot="footer" class="dialog-footer">
			<el-button @click="dialogVisible = false">取 消</el-button>
			<el-button type="primary" @click="configUserRoles">确 定</el-button>
			</span>
		</el-dialog> -->
		<el-dialog title="配置管理员" v-model="dialogVisible" size="tiny">
		  <div class="select-tree">
			<div v-for="user in userList" :key="user.userId">
			<input
				type="radio"
				:id="user.userId"
				:value="user.userId"
				v-model="selectUser"
			/>
			<label class="u-label" :for="user.userId">{{ user.userName }}</label>
			</div>
		  </div>
		  <span slot="footer" class="dialog-footer">
			<el-button @click="dialogVisible = false">取 消</el-button>
			<el-button type="primary" @click="configUserRoles">确 定</el-button>
			</span>
		</el-dialog>
		<!-- <el-dialog title="配置管理员" v-model="dialogVisible" size="tiny">
		    <div class="radio-container">
				<div class="radio-group" v-for="(user, index) in userList" :key="index">
					<input
						type="radio"
						:id="user.userId"
						:value="user.userId"
						v-model="selectUser"
						class="radio-button"
					/>
					<label :for="user.userId" class="radio-label">{{ user.userName }}</label>
				</div>
			</div>
		  	<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="configUserRoles">确 定</el-button>
			</span>
		</el-dialog> -->
	  </div>
  
  
	</imp-panel>
  </template>
  
  <script>
	import panel from "../../components/panel.vue"
	import * as api from "../../api"
	import * as sysApi from '../../services/sys'
	var mApp;
	export default {
	  components: {
		'imp-panel': panel
	  },
	  data(){
		return {
		  userInfo:null,
		  currentRow: {},
		  dialogVisible: false,
		  dialogLoading: false,
		  defaultProps: {
			children: 'children',
			label: 'name',
			id: "id",
		  },
		  roleTree: [],
		  companyLististLoading: false,
		  searchKey: '',
		  tableData: {
			pagination: {
			  total: 0,
			  pageNo: 1,
			  pageSize: 10,
			  parentId: 0
			},
			rows: []
		  },
		  userList:[],
		  selectUser:-1
		}
	  },
	  created(){
		mApp = this;
		let item = window.sessionStorage.getItem("user-info");
		console.log("user.vue:", item);
		if (!!item){
			this.userInfo = JSON.parse(item);
		}
		this.loadData();
	  },
	  methods: {
		loadData(){
			mApp.companyLististLoading = true;
			sysApi.companyList({})
			.then(res => {
			  this.tableData.rows = res.data;
			  mApp.companyLististLoading = false;
			  console.log("****company list:", this.tableData.rows[0].adminUser);
			  // this.tableData.pagination.total = res.total;
			}).catch(res=>{
				mApp.companyLististLoading = false;
			});
		},
		
		handleRoleConfig(index, row){
		  this.currentRow = row;
		  console.log("***config company", this.currentRow);
		  if(this.currentRow.adminUser){
			this.selectUser = this.currentRow.adminUser.userId;
		  }
		  this.dialogVisible = true;
		  if (this.userList.length <= 0) {
			sysApi.userList({})
			  .then(res => {
				this.userList = res.data;
			  })
		  }
		},
		configUserRoles(){
		  let checkedKey = this.selectUser;
		  console.log("**select:", checkedKey);
		  sysApi.companyConfig({
			userId:checkedKey,
			companyName:this.currentRow.companyName
		  })
		  .then(res => {
			if(res.code == 0){
				this.$message('修改成功');
		  		this.dialogVisible = false;
			}else{
				this.$message('修改失败');
		  		this.dialogVisible = false;
			}
		  	
		  }).catch(res => {
			this.$message('修改失败');
		  	this.dialogVisible = false;
		  });
		},
		search(target){
		  this.loadData();
		},
		handleSelectionChange(val){
  
		},
		handleSizeChange(val) {
		  this.tableData.pagination.pageSize = val;
		  this.loadData();
		},
		handleCurrentChange(val) {
		  this.tableData.pagination.pageNo = val;
		  this.loadData();
		},
		handleEdit(index, row){
		  this.$router.push({path: 'companyAdd', query: {id: row.companyId}})
		},
		handleDelete(index, row){
			console.log("***item:", row);
		//   this.$http.get(api.SYS_USER_DELETE + "?userIds=" + row.id).then(res => {
		// 	this.loadData();
		//   });
		},
		
	  },
	  
	}
  </script>
  <style>
	.el-pagination {
	  float: right;
	  margin-top: 15px;
	}

	.u-label {
        word-break: break-all;
        padding: 10px 8px;
        font-size: 20px;
        line-height: 20px;
        display: inline-block;
      }
	  .radio-container {
  display: flex;
  flex-direction: column;
}
 
.radio-group {
  position: relative;
  margin-bottom: 10px; /* 分割线的高度 */
}
 
.radio-button {
  display: none;
}
 
.radio-label {
  display: block;
  padding-left: 25px; /* 同radio按钮大小，为了对齐 */
  height: 20px; /* 同radio按钮大小 */
  line-height: 20px; /* 同radio按钮大小 */
  cursor: pointer;
}
 
.radio-label:before {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 20px; /* 单选框大小 */
  height: 20px; /* 单选框大小 */
  content: '';
  border: 1px solid #ccc; /* 边框颜色 */
  border-radius: 50%; /* 单选框圆形 */
}
 
.radio-button:checked + .radio-label:before {
  content: '✔';
  text-align: center;
  line-height: 20px;
  color: white;
  background-color: #007bff; /* 选中的背景颜色 */
}
 
/* 分割线样式 */
.radio-group:not(:last-child):after {
  position: absolute;
  content: '';
  left: 50%;
  bottom: 0;
  width: 50%; /* 分割线宽度 */
  height: 10px; /* 分割线高度 */
  background-color: #ccc; /* 分割线颜色 */
}
  </style>
  


