<template>
  <!-- <div style="position:absolute"> -->
  <!-- <imp-panel> -->
  <div class="bodyStyle">
    <div class="tableFrom" v-show="true">
      <div class="titleStyle">
        <p >派单信息</p>
      </div>
      <el-table
        :data="orderList"
        v-loading="orderListLoading"
        style="width: 100%"
        border
        v-show="true">
        <el-table-column prop="number" label="序号" width="100">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.orderId }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="company" label="公司名称">
          <template slot-scope="scope">
            <span >{{ scope.row.companyName }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="IP地址">
          <template slot-scope="scope">
            <span >{{ scope.row.ipAddress }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="from" label="派单公司名称">
          <template slot-scope="scope">
            <span >{{ scope.row.clientCompanyName }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="日期">
          <template slot-scope="scope">
            <!-- <i class="el-icon-time"></i> -->
            <span >{{ scope.row.orderTime }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="承接状态">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ getStatusText(scope.row.acceptanceStatus) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleButton(scope.$index, scope.row)">{{ getButtonText(scope.row.status) }}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="tableChrome">
      <div class="titleStyle">
        <p >插件状态</p>
      </div>
      <el-table
        :data="chromeList"
        style="width: 100%">
        <el-table-column label="序号" width="100">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="公司名称">
          <template slot-scope="scope">
            <span >{{ scope.row.company }}</span>
          </template>
        </el-table-column>
        <el-table-column label="IP地址">
          <template slot-scope="scope">
            <span >{{ scope.row.ipAddress }}</span>
          </template>
        </el-table-column>
        <el-table-column label="浏览器监测状态">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ getChromeStatusText(scope.row.status) }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    
  </div>
<!-- </imp-panel> -->
</template>
<script >
import panel from "../components/panel.vue"
import * as api from "../api"
import * as sysApi from '../services/sys'

var mApp;
export default {
  components: {
      'imp-panel': panel
    },
  data() {
    return {
      mAudio:null,
      reportnum: null,
      reportToday: 0,
      // hospitalNum: null,
      // hospitalNumToday: 0,
      chromeList: [],
      orderList: [],
      userInfo:null,
      lastTime:0,
      orderListLoading:true,
    }
  },
  created(){
    mApp = this;
    let item = window.sessionStorage.getItem("user-info");
    console.log("header.vue---user:", item);
    if (!!item){
        this.userInfo = JSON.parse(item);
    }
    this.mAudio = new Audio(require('../../static/audio/y682.mp3')); // 假设你的音频文件放在src/assets/sounds目录下
    this.loadOrder(true);
    setInterval(function (){
      mApp.loadOrder(false);
    }, 5000);
    
  },
  mounted() {
    // this.loadOrder(true);
    // setInterval(function (){
    //   mApp.loadOrder(false);
    // }, 5000);
  },
  methods: {
    playSound(){
      this.mAudio.play();
    },
    loadOrder(showLoading){
      if(showLoading){
        mApp.orderListLoading = true;
      }
      
      sysApi.queryCompanyOrderList({userId:this.userInfo.userId})
            .then(res => {
              
              console.log("*********loadOrder success******:", this.lastTime, res);
              var table = res.data;
              var newData = false;
              var curDate = 0;
              this.orderList = table;
              table.forEach(function(element) {
                console.log(element);
                curDate = (element.createTime).valueOf();
                if(mApp.lastTime < curDate){
                  newData = true;
                }
              });
              mApp.orderListLoading = false;
              if(newData){
                this.playSound();
              }
              
              // this.lastTime = curDate;
            }).catch(err => {
              mApp.orderListLoading = false;
              console.log("*********loadOrder error******:", err);
            })
    },

    handleEdit(index, row) {
        console.log(index, row);
      },
    handleButton(index, row) {
      console.log(index, row);
      var orderId = row.orderId;
      sysApi.acceptOrder(orderId).then(res => {
        console.log("****acceptOrder***", res);
        mApp.loadOrder();
      }).catch(err => {
        console.log("****acceptOrder err***", err);
      });

    },
    getStatusText(status){
      var message = "待接单";
      switch (status){
        case 0:
          message = "待接单";
          break;
        case 1:
          message = "制作中";
          break;
        case 2:
          message = "完成";
          break;
        case 3:
          message = "失败";
          break;
      }
      return message;
    },
    getButtonText(status){
      var message = "承接";
      switch (status){
        case 0:
          message = "承接";
          break;
        case 1:
        case 2:
          message = "完成";
          break;
      }
      return message;
    },
    getChromeStatusText(status){
      var message = "监测正常";
      switch (status){
        case 0:
          message = "监测正常";
          break;
        case 1:
          message = "监测异常";
          break;
      }
      return message;
    },
  }
}
</script>
<style >
.bodyStyle{
  background-color: #ffffff;
  position: relative;
}
.tableFrom {
  width: 100%;
  height: 40vh;
  overflow-y: auto
}
.tableChrome {
  width: 100%;
  height: 40vh;
  overflow-y: auto;
  padding-top: 20px;
}
/* 直接使用类名来覆盖 */
.el-table th {
  background-color: #f0f0f0; /* 表头背景色 */
  color: #606266; /* 表头字体颜色 */
}
.titleStyle{
  display: flex;
  align-items: center; /* 垂直居中 */
  justify-content: flex-start; /* 水平居左 */
  height: 30px; /* 容器高度，可以根据需要调整 */
  background-color:#d1dddb;
  padding-left: 8px;
  /* font-style: oblique; */
  /* font-weight: bold; */
}

.container {
  width: 100%;

  .work1,
  .work2 {
    position: absolute;
    left: 100px;

    .work_row {
      position: absolute;
      height: 100%;
      width: 100%;

      .img {
        margin-top: 30px;
        margin-left: 50px;
      }

      img {
        position: absolute;
        left: 50px;
      }

      .num {

        color: #fff;
        height: 30px;
        width: 100px;
        position: absolute;
        font: 14px Helvetica Neue, Helvetica, PingFang SC, Tahoma, Arial, sans-serif;

        top: 90px;
        left: 40px;
      }

      .desc {
        height: 28px;
        width: 270px;
        position: absolute;
        right: 0;
        text-align: left;
        color: white;
        font-size: 15px;
      }

      .total {
        height: 28px;
        width: 270px;
        position: absolute;
        right: 0;
        text-align: left;
        margin-top: 47px;
        color: white;
        font-size: 17px;
      }
    }
  }

  .work2 {
    position: absolute;
    left: 632px !important;

  }

  #work {
    margin-top: 10px;
    width: 480px;
    height: 160px;
    border: 1px solid rgb(232, 232, 232);
    float: left;
    border-radius: 3px;
    background: rgb(65, 178, 166);
  }

}
</style>


